  width: 12px;
  height: 12px;
  background-color: var(--online-indicator);
  border-radius: 50%;
}
.pulsating_dot::before, .pulsating_dot::after {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: "";
}
.pulsating_dot::after {
  animation: pulse1 2s ease 0s infinite;
}
